import * as React from "react"
import PropTypes from "prop-types"

import Header from "../header"
import Hero from "../hero"
import Sponsoren from "../sponsoren"
import { GatsbyImage, getImage } from "gatsby-plugin-image"

const NewsLayout = ({ children, frontmatter }) => {

  let image = null
  let alt = null
  if (frontmatter.images) {
    image = frontmatter.images[0]
    alt = image.caption | image.name | frontmatter.title
  }

  return (
    <>
    <Header/>
    <Hero title={frontmatter.title}/>

    <div className="container">
        <section className="articles">
            <div className="column is-8 is-offset-2">
              <div className="card article my-2">
                {(image) && (
                <div className="card-image">
                  <GatsbyImage
                    image={getImage(image.url.childImageSharp.full)}
                    alt={alt}
                  />
                </div>
                )}
                <div className="card-content">
                  <div className="content article-body">{children}</div>
                  <div className="media">
                    <div className="media-content has-text-centered">
                      <div className="tags level-item">
                      {(frontmatter.tags) && (
                        frontmatter.tags.map(tag => (
                            <span className="tag is-rounded is-light is-danger">{tag}</span>
                        ))                        
                      )}
                      {(frontmatter.date) && (
                        <span className="tag is-rounded">{frontmatter.date}</span>
                      )}
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
        </section>
    </div>

    <footer className="footer has-text-centered">
      <Sponsoren/>
    </footer>
    </>
  )
}

NewsLayout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default NewsLayout
